<template>

  <div>
    <div class="title"><img src="../imgs/left.png" alt=""/></div>
    <div class="notes">
      
      
    </div>
    <div class="card">
      <span></span>

      <div class="imgs">
        <img src="../imgs/card.png" alt=""/>
      </div>
      <p></p>
    </div>
    <div class="openCard"><button></button></div>
  </div>


</template>

<script type="text/ecmascript-6">
  export default{
    name: 'Login',
    data (){
    return {}
  },

  methods: {
  }
  }
</script>

<style>
  @import "../css/base.css";

  body {

    background-color: #f1f1f1;
  }

  .title {
    position: relative;
    height: 45px;
    background-color: #fff;
    line-height: 45px;
    font-size: 15px;
  }

  .title img {
    position: absolute;
    width: 10px;
    height: 15px;
    left: 20px;
    top: 15px;
  }

  .notes {
    text-align: left;
    width: 100%;
    margin: 10px 0;
    color: #eb3984;
    padding: 10px 16px;
    background-color: #fff;
  }

  .card {
    background-color: #fff;
    text-align: left;
    width: 100%;
    padding: 16px;
  }

  .card span {
    font-size: 15px;
  }

  .card .imgs {
    margin: 12px 0;
    width: 228px;
    height: 146px;
  }

  .card .imgs img {

    width: 100%;
    height: 100%;
  }

  p {
    line-height: 20px;
    font-size: 12px;
  }

  .openCard {
    position: fixed;
    left: 0;
    bottom: 0;
    background-color: #fff;
    width: 100%;
    height: 42px;
    line-height: 42px;
  }
  .openCard button{
    width: 126px;
    height: 32px;
    margin: 5px auto;
    background-color: #eb3984;border-radius: 6px;
    color: #fff;
  }
</style>



