<style>
  @import "../css/base.css";

  body {
    background-color: #fff;
  }

  .title {
    position: relative;
    height: 45px;
    background-color: #fff;
    line-height: 45px;
    font-size: 15px;
  }

  .title img {
    position: absolute;
    width: 10px;
    height: 15px;
    left: 20px;
    top: 15px;
  }

  input {
    background-color: #e5e5e5;
    display: block;
    width: 95%;
    height: 36px;
    margin: 10px auto 0;
    border-radius: 5px;
    padding-left: 10px;
  }

  .pic {

    width: 100%;
    height: 282px;
  }

  .pic img {
    width: 100%;
    height: 100%;
  }

  .prices {
    text-align: left;
    height: 35px;
    background-color: #eb3984;
    line-height: 35px;
    padding: 0 16px;
    color: #fff;
  }

