  }

  /**/
  .introduct {
    text-align: left;
    padding: 10px 16px;
  }

  .introduct .name {
    height: 42px;
    display: flex;
  }

  .introduct .name p {
    flex: 8;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }

  .introduct .name .share {
    font-size: 12px;
    color: #eb3984;
    margin-left: 16px;
    text-align: center;
    flex: 2;
  }

  .introduct .name .share img {
    width: 18px;
    height: 20px;
  }

  .status {
    margin-top: 6px;
    color: #8f8f8f;
    font-size: 12px;
  }

  .status span {
    margin-right: 15px;
  }

  .status span:last-of-type {
    float: right;
    margin-right: 10px;
  }
/*tuijian*/
  .Recommend {
    overflow: hidden;
    font-size: 13px;
    padding-top: 20px;;
    height: 100px;
  }

  .Recommend span {
    margin-right: 20px;
    border-radius: 3px;
    font-size: 12px;
    text-align: center;
    display: block;
    width: 32px;
    line-height: 18px;
    border: 1px solid #eb3984;
