    /*height: 380px;*/
   margin-bottom: 45px;

  }
  .picshow img{
    width: 100%;
    height: 380px;
  }
  .bottom{
    position: fixed;
    bottom: 0;left: 0;
    padding: 0 16px ;
    border-top: 1px solid #ededed;
    height:45px;
    line-height: 45px;
    text-align: left;
    width: 100%;
    background-color: #fff;

  }
  .bottom span:first-of-type{
     color: #eb3984;
   }
  .bottom span:last-of-type{
   position: fixed;
    right: 110px;
    color: #8f8f8f;
  }

  .bottom button{
    float: right;
    background-color:#eb3984;
    display: block;
    width: 88px;
    height: 26px;
    line-height: 26px;
    border-radius: 4px;
    margin-top: 10px;
    color: #FFF;
  }


</style>



