<template>

  <div>
    <div class="title"><img src="../imgs/left.png" alt=""/></div>
    <div class="useMessage">
      <div class="headimg">
        <img src="../imgs/head.png"/>

        <div class="name"><span></span>
          <span></span>

        </div>
      </div>
      <img class="dele"  src="../imgs/cha.png" alt=""/>
    </div>
    <div class="add">
      
    </div>
    <div class="tips">
      <span></span>
      <p></p>
    </div>
    <button></button>


  </div>


</template>

<script type="text/ecmascript-6">
  export default{
    name: 'Login',
    data (){
      return {}
    },

    methods: {}
  }
</script>

<style>
  @import "../css/base.css";

  body {
    background-color: #f1f1f1;
  }

  .title {
    position: relative;
    height: 45px;
    background-color: #fff;
    line-height: 45px;
    font-size: 15px;
  }

  .title img {
    position: absolute;
    width: 10px;
    height: 15px;
    left: 20px;
    top: 15px;
  }

  .useMessage {
position: relative;
    margin-top: 10px;
    padding: 0 30px 0 20px;
    width: 100%;
    height: 58px;
    background-color: #fff;
    margin-bottom: 1px;
  }

  .useMessage img {
    margin-top: 9px;
    float: left;
    width: 40px;
    height: 40px;
  }

  .name {
    text-align: left;
    margin-left: 50px;
    padding: 10px;
  }
  .useMessage .dele{
    position: absolute;
    right: 12px;
    top: 23px;
    width: 12px;
    height: 12px;
    margin-top: 0;
  }

  .name span {
    overflow: hidden;
    height: 20px;
    display: block;
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .name span:last-of-type {
    color: #f94190;
    font-size: 12px;
    margin-top: 4px;
  }

  .add {
    width: 100%;
    height: 48px;
    line-height: 48px;
    background-color: #fff;
    color: #f94190;
  }
  .tips{
    text-align:left;
    padding: 12px 20px;
    color: #989898;
  }
  .tips p{
    /*font-size: 13px;*/
    margin-top: 8px;
  }
  button{
    width: 166px;
    height: 36px;
    line-height: 36px;
    background: #fff;
    border-radius: 5px;
    margin-top: 48px;
  }

</style>



