<template>

  <div>
    <div class="title"><img src="../imgs/left.png" alt=""/></div>
    <div class="addAddress"><img src="../imgs/address1.png" class="local" alt=""/><span></span>
      <img class="right" src="../imgs/right.png" alt=""/>
    </div>
    <div class="order">
      <div class="storeName">
        <img src="../imgs/store.png" alt=""/><span></span>
      </div>
      <div class="product">
        <img src="../imgs/pic.png" alt=""/>

        <div class="proName"><h5></h5>

          <div class="guige">11g</div>
        </div>
        <div class="price">
          <span>295.00</span>
          <span>0.00</span>
        </div>

      </div>

    </div>
    <div class="freight"><span></span><span>$12.00</span></div>
    <div class="freight"><span></span><span>$295.00</span></div>
    <input type="text" placeholder=""/>

    <div class="bottom">
 <span class="price">$16.00</span><span></span>
      <button></button>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    name: 'Login',
    data (){
      return {}
    },

    methods: {}
  }
</script>

<style>
  @import "../css/base.css";

  body {
    background-color: #f1f1f1;
  }

  .title {
    position: relative;
    height: 45px;
    background-color: #fff;
    line-height: 45px;
    font-size: 15px;
    border-bottom: 2px dashed #ed1d24;
  }

  .title img {
    position: absolute;
    width: 10px;
    height: 15px;
    left: 20px;
    top: 15px;
  }

  .addAddress {
    text-align: left;
    padding: 0 16px;
    height: 56px;
    line-height: 56px;
    background-color: #fff;
    margin-bottom: 17px;
  }

  .addAddress .local {
    margin-right: 13px;
    width: 14px;
    height: 20px;
  }

  .addAddress .right {
    float: right;
    width: 8px;
    height: 15px;
    margin-top: 20px;

  }

  .order {
    text-align: left;
    background-color: #fff;
  }

  .storeName {
    padding: 0 16px;
    width: 100%;
    height: 34px;
    line-height: 34px;
    border-bottom: 1px solid #ededed;
    text-align: left;
  }

  .storeName img {
    width: 20px;
    height: 16px;
    margin-right: 7px;
  }

  .order .product {
    display: flex;
    padding: 10px 16px;
    height: 110px;
    background-color: #fff;
  }

  .order .product img {
    width: 86px;
    height: 86px;
    margin-right: 10px;
    float: left;
  }

  h5 {
    margin-bottom: 5px;
    height: 40px;
    line-height: 19px;
    float: left;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    font-weight: normal;
  }

  .order .product .proName {
    flex: 4;
  }

  .order .product .price {
    text-align: right;
    margin-left: 6px;
    flex: 1;
    float: left;
    height: 40px;
  }

  .order .product .price span {
    display: block;
    width: 100%;
  }

  .order .product .price span:first-of-type {
    text-decoration: line-through;
    color: #8f8f8f;
  }

  .guige {
    font-size: 12px;
    color: #8f8f8f;
  }

  .freight {
    width: 100%;
    background-color: #fff;
    text-align: left;
    padding: 0 16px;
    height: 36px;
    line-height: 36px;
    border-top: 1px solid #ededed;
  }

  .freight span:last-of-type {
    float: right;
  }

  input {
    background-color: #e5e5e5;
    padding-left: 10px;
    border-radius: 3px;
    height: 32px;
    width: 92%;
    margin: 10px 16px;
  }

  .bottom {
    padding-left: 16px;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 46px;
    line-height: 46px;
    background-color: #fff;
    border-top: 1px solid #ededed;
    text-align: left;
  }
  .bottom span:last-of-type{
    position: fixed;right: 96px;
    color: #8f8f8f;
    font-size: 12px;
  }
  .bottom span:first-of-type{

    color: #fb1678;
    font-size: 15px;
  }
  .bottom button{
    color: #fff;
    width: 86px;
    height: 100%;
    background-color: #fb1678;float: right;
  }



</style>



